.cursive {
    font-family: "Amatic SC", cursive;
    font-weight: 700;
}

.markdown-p {
    font-size: 0.5em;
    font-family: 'Arial', sans-serif;
    /* font-size: 1em; */
    line-height: 1.6;
}

.video-responsive {
    overflow: hidden;
    padding-bottom: 56.25%;
    position: relative;
    height: 0;
}

.video-responsive iframe {
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    position: absolute;
}

.App {
    text-align: center;
    background: black;
}

.App-logo {
    height: 80vmin;
    pointer-events: none;
}

.label-logo {
    height: 80vh;
    pointer-events: none;
}

a {
    text-align: center;
    /* font-weight: 600; */
    color: darkred;
    text-decoration: underline;

}

/* #FFD99B; */

.App-header {
    background-color: black;
    /*  // rgb(76, 76, 76); */
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: Black;
}


.App-link {
    color: #61dafb;
}

.fill-window {
    height: 100%;
    position: absolute;
    left: 0;
    width: 100%;
    overflow: hidden;
}


.my-node-enter {
    opacity: 0;
}

.my-node-enter-active {
    opacity: 1;
    transition: opacity 200ms;
}

.my-node-exit {
    opacity: 1;
}

.my-node-exit-active {
    opacity: 0;
    transition: opacity 200ms;
}